@import url(https://fonts.googleapis.com/css2?family=Lexend&display=swap);
:root{
    --blue:#58a6ff;
}
*{
    margin: 0;
    padding: 0;
    outline: 0;
    text-decoration: none;
    border: none;
    font-family: 'Lexend',sans-serif;
    box-sizing: border-box;
}
body{
    background-color: #f3f4f7;
}
.App{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
    width: 100%;
}
.App .main{
    height: 100%;
    padding: 20px 0;
}
.App .main .header{
    text-align: center;
}
.App .main .header .title{
    color: #58a6ff;
    color: var(--blue);
    font-size: 25px;
    font-weight: 700;
}
.App .main .header .search{
    width: 100%;
    margin: 10px 0;
    height: 30px;
    width: 100%;
    border-radius: 30px;
    padding: 0 10px;
    border: 2px rgb(212, 210, 210) solid;
    transition: all 400ms;
    font-size: 17px;
}
.App .main .header .search:focus{
    border-color: #58a6ff;
    border-color: var(--blue);
}
.App .main .user{
    height: 70%;
}
.App .main .user .header{
    text-align: center;
}
.App .main .user .header img{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
}
.App .main .user .header .user-name{
    display: block;
    color: #58a6ff;
    color: var(--blue);
    font-weight: 500;
}
.App .main .user .info .user-info{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-size: 20px;
}
.App .main .user .info .user-info svg{
    fill: #58a6ff;
    fill: var(--blue);
}
